import { Component, OnInit } from '@angular/core';
import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { BudgetAnggaranService } from 'src/app/sdk/asset/services';

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private budgetAnggaranService: BudgetAnggaranService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService
    ) { }

    pageIndex = 1
    pageSize = 10
    total = 1
    listOfData = []
    loading = true
    sortValue: string = null
    sortKey: string = null
    search: string = ''

    ngOnInit() {
        this.searchData()
    }
    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key
        this.sortValue = sort.value
        this.searchData()
    }

    searchData(reset: boolean = false): void {
        if (reset) {
            this.pageIndex = 1
        }
        this.loading = true
        this.budgetAnggaranService.getBudgetAnggarans(JSON.stringify({
            pageIndex: this.pageIndex, pageSize: this.pageSize, sortKey: this.sortKey, sortValue: this.sortValue, search: this.search
        }))
            .subscribe((data: any) => {
                this.loading = false
                this.total = data.data.total
                this.listOfData = data.data.data
                console.log(data.data.data)
            })
    }

    delete(id) {
        this.spinner.show();
        this.budgetAnggaranService.deleteBudgetAnggaransId(id).subscribe(
            data => {
                this.searchData();
                this.spinner.hide();
            }
        )
    }

    batalDelete() {
        this.nzMessageService.info('click cancel');
    }


}
