import { Component, OnInit } from '@angular/core';
import { Anggaran } from 'src/app/sdk/asset/models';
import { BudgetAnggaranService } from 'src/app/sdk/asset/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';
import { Divisi, Departemen, Kantor } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';
import { CompanyKantorService, CompanyDepartemenService, CompanyDivisiService } from 'src/app/sdk/hr/services';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private budgetAnggaranService: BudgetAnggaranService,
        private companyKantorServisce: CompanyKantorService,
        private companyDepartemenServisce: CompanyDepartemenService,
        private companyDivisiServisce: CompanyDivisiService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router
    ) { }

    dataBudget: Anggaran = {}

    listDivisi: Divisi[] = []
    listDepartemen: Departemen[] = []
    listKantor: Kantor[] = []


    ngOnInit() {
        this.form()
        this.getPreeData()



    }

    getPreeData() {
        this.companyDepartemenServisce.getCompanyDepartemens().subscribe(
            data => {
                this.listDepartemen = data.data;
            }
        )


        this.companyKantorServisce.getCompanyKantors().subscribe(
            data => {
                this.listKantor = data.data;
            }
        )
    }

    getDivisi(){
        this.companyDivisiServisce.getCompanyDivisis(JSON.stringify({id_departemen: this.dataForm.value.id_departement})).subscribe(
            data => {
                this.listDivisi = data.data;
            }
        )
    }


    dataForm: FormGroupTypeSafe<Anggaran>;
    form() {
        this.dataForm = this.fb.group<Anggaran>(
            {
                tahun: new FormControl('', [Validators.required]),
                id_departement: new FormControl(''),
                id_kantor: new FormControl(''),
                id_divisi: new FormControl(''),

            }
        )
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }


        this.dataBudget = this.dataForm.value;
        this.budgetAnggaranService.postBudgetAnggarans(this.dataBudget).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )
    }


    goToBack() {

        this.router.navigate(['/hr/direktori/general/anggaran/list'])
    }

}
